export class SchWeekTimeViewModel {
  /// <summary>
  ///  排程編號
  /// <summary>
  public sch_no: string;
  /// <summary>
  ///  排程名稱
  /// <summary>
  public sch_name: string;
  /// <summary>
  ///   每星期組合數相加(1:星期日,2:星期一,4:星期二,8:星期三,16:星期四,32:星期五,64:星期六)
  /// <summary>
  public freq_interval: number;
  /// <summary>
  ///   每星期組合數說明(1:星期日,2:星期一,4:星期二,8:星期三,16:星期四,32:星期五,64:星期六)
  /// <summary>
  public freq_interval_desc: number;

  /// <summary>
  /// 星期日
  /// </summary>
  public Sun: boolean;

  /// <summary>
  /// 星期一
  /// </summary>
  public Mon: boolean;

  /// <summary>
  /// 星期二
  /// </summary>
  public Tue: boolean;

  /// <summary>
  /// 星期三
  /// </summary>
  public Wed: boolean;

  /// <summary>
  /// 星期四
  /// </summary>

  public Thu: boolean;

  /// <summary>
  /// 星期五
  /// </summary>
  public Fri: boolean;

  /// <summary>
  /// 星期六
  /// </summary>
  public Sat: boolean;
  /// <summary>
  /// 間隔數
  /// </summary>
  public freq_subday_interval: number;
  /// <summary>
  ///  每日間隔單位 2(秒),4(分鐘),8(小時)
  /// </summary>
  public freq_subday_type: number;

  /// <summary>
  ///  每日間隔單位說明 2(秒),4(分鐘),8(小時)
  /// </summary>
  public freq_subday_type_desc: string;
  /// <summary>
  /// 每隔幾周數
  /// </summary>
  public freq_recurrence_factor: number;
  /// <summary>
  ///  開始作業日期
  /// <summary>
  public active_start_date: string;
  /// <summary>
  ///  停止作業日期
  /// <summary>
  public active_end_date: string;
  /// <summary>
  ///  開始作業時間
  /// <summary>
  public active_start_time: string;
  /// <summary>
  ///  停止作業時間
  /// <summary>
  public active_end_time: string;
  /// <summary>
  ///  啟用註記
  /// <summary>
  public is_active: boolean;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;
  /// <summary>
  ///  作業數
  /// <summary>
  public job_cnt: number;
  /// <summary>
  ///  排程描述
  /// <summary>
  public sch_description: string;
  /// <summary>
  ///  啟用註記說明
  /// <summary>
  public is_active_desc: string;
  /// <summary>
  ///  啟用註記顏色
  /// <summary>
  public is_active_color: string;
  /// <summary>
  ///  啟用註記顏色
  /// <summary>
  public is_active_color_right: string;
  /// <summary>
  ///  作業數顏色
  /// <summary>
  public job_color: string;

}



